<template>
  <div class="bottom-bar">
    <div class="bar-item bar-left">
      <div>
        <i class="icon service"></i>
        <span class="text">客服</span>
      </div>
      <div @click="selectChange">
        <i class="icon select" :class="{selectaction : isSelectZ }"></i>
        <span class="text">收藏</span>
      </div>
    </div>
    <div class="bar-item bar-right">
      <div class="cart" @click="addCarts">加入购物车</div>
      <div class="buy" @click="buyCake">￥{{gprice}}  购买</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'DetailBottom',
    props: {
      //判断是否收藏
        isSelect: {
          type: Boolean,
          default: false
        },
        //商品的价格
        gprice:{
          type: Number,
          dafault: 0
        }
    },
    data () {
      return {
          //判断是否收藏
          isSelectZ: this.isSelect
      }
    },
    computed: {
      //获取用户信息
      getUserInfo() {
        return this.$store.state.userInfo
      }
    },
    watch: {
      isSelect(newVal){
         this.isSelectZ = newVal;
      }
    },
    methods: {
      //加入购物车
      addCarts() {
        if(this.getUserInfo.uid == 0){
           this.$toast.show("请先登录")
        }else {
          //发送事件，并且修改将用户id传递过去
           this.$emit('addCarts', this.getUserInfo.uid)
        }
       
      },
      //收藏的点击事件
      selectChange() {
        if(this.getUserInfo.uid == 0){
          this.$toast.show("请先登录")
        }else {
          //将是否收藏取反 向父组件发送事件 修改父组件中的值
          this.isSelectZ = !this.isSelectZ;
          this.$emit('selectChange', this.isSelectZ)
        }
      },
      //购买蛋糕
      buyCake() {
        if(this.getUserInfo.uid == 0){
          this.$toast.show("请先登录")
        }else {
          this.$emit('insOrder')
        }
      }
    }
  }
</script>

<style scoped>
  .bottom-bar{
    height: 49px;
   position: absolute;
   bottom: 0px;
   left: 0px;
   right: 0px;
   display: flex;
   text-align: center;
  }
   .bar-item {
    flex: 1;
    display: flex;
  }

  .bar-item>div {
    flex: 1;
  }
/*左侧的文字样式 */
  .bar-left .text {
    font-size: 13px;
  }
/*左侧的图标样式 */
  .bar-left .icon {
    display: block;
    width: 22px;
    height: 22px;
    margin: 5px auto 3px;
    background: url("~assets/img/detail_bottom.png") 0 0/100%;
  }
  /*左侧的收藏活跃时的图片 */
  .bar-left .selectaction{
    background-position: 0 -27px;
  }
/*左侧的客服图片 */
  .bar-left .service {
    background-position:0 -54px;
  }
/*右侧的div样式 */
  .bar-right {
    font-size: 15px;
    color: #fff;
    line-height: 58px;
  }
  /*购物车样式 */
  .bar-right .cart {
    background-color: #ffe817;
    color: #333;
  }
  /*购买样式 */
  .bar-right .buy {
    background-color: #f69;
  }
</style>