//用来请求收藏商品的参数
import {request} from './request'

//获取收藏商品的cid
export function getCollectCid(gid,uid) {
  return request({
    url: 'getCollectCid.do',
    method: 'post',
    data:{
      gid,uid
    }
  }) 
}
//收藏商品
export function addColect(uid, gid, cgtitle, cgprice, cgimg,cgcollect){
  return request({
    url: 'addColect.do',
    method: 'post',
    data: {
      uid, gid, cgtitle, cgprice, cgimg,cgcollect
    }
  })
}
//取消收藏
export function delColect(cid,cgcollect,gid){
  return request({
    url: 'delColect.do',
    method: 'post',
    data: {
      cid,cgcollect,gid
    }
  })
}
//获取收藏商品
export function getAllCollectById(uid) {
  return request({
    url: 'getAllCollectById.do',
    method: 'post',
    data: {
      uid
    }
  })
}