<template>
  <nav-bar>
    <div slot="left" class="nav-left" @click="backClick">
      <img src="~assets/img/back.svg" alt="">
    </div>
    <div slot="center" class="nav-titles">
      <div v-for="(item, index) in titles" :key="index" class="item-titles" :class="{active: index == currentIndex}" @click="navItemClick(index)">
        {{item}}
      </div>
    </div>
  </nav-bar>
</template>

<script>
  import NavBar from 'common/navbar/NavBar'
  export default {
    name: 'DetailNav',
    components: {
      NavBar
    },
    data() {
      return {
        titles: ['商品', '参数', '推荐'],
        currentIndex: 0         //设置用来保存标题栏的状态
      }
    },
    methods: {
      //点击事件
      navItemClick(index) {
        this.currentIndex = index;
        //向父组件发送消息
        this.$emit('titleClick', index)
      },
      //点击返回
      backClick() {
        this.$router.back()
      }
    }
  }
</script>

<style scoped>
  .nav-titles{
    display: flex;
  }
  .item-titles{
    flex:1;
    font-size: 14px;
  }
  .active{
    color: #ff8198;
  }
  .nav-left{
    margin-top: 5px;
  }
</style>