<template>
  <div id="detail">
    <detail-nav @titleClick="titleClick" ref="nav"></detail-nav>
    <div v-if="isGoodsHave">
       <scroll class="det-con" ref="scroll" @scroll="pScroll" :probe-type="3" @pullingUp="loadMore" :pull-up-load="true">
        <detail-swiper :top-images="topImages"></detail-swiper>
        <detail-base :goods="goodsBase"></detail-base>

        <detail-kinds ref="kinds" :goods-item="goodsBase" @changessactive= "changessactive"></detail-kinds>

        <div ref="recommend" class="recommends-goods">推荐商品</div>
        <goods-list :goods="goodsRecommend.list"></goods-list>
      </scroll>
      <!--置顶按钮   监听组件根元素点击要用native-->
      <back-top @click.native="backClick" v-show="isShowBackTop"></back-top>
      <detail-bottom @addCarts="addCarts" :is-select="isSelect" :gprice="getGprice" @selectChange="selectChange" @insOrder="insOrder"></detail-bottom>
    </div>
    <div class="delgood" v-else>商品已下架</div>
  
  </div>
</template>

<script>
  import DetailNav from './childComps/DetailNav'
  import DetailSwiper from './childComps/DetailSwiper'
  import DetailBase from './childComps/DetailBase'
  import DetailKinds from './childComps/DetailKinds'
  import DetailBottom from './childComps/DetailBottom'
  import Scroll from 'common/scroll/Scroll'
  import GoodsList from 'content/goods/GoodsList'
  import {itemImageMixin, backTopMixin} from 'commons/mixin'
  import {debounce} from 'commons/utils'
  import {getDetails} from 'network/detail'
  import {getHomeGoods} from 'network/home'
  import {getCollectCid,addColect,delColect} from 'network/collect'
  import {insOrder} from 'network/order'

  export default {
    name: 'Detail',
    components: {
      DetailNav,
      DetailSwiper,
      DetailBase,
      Scroll,
      DetailKinds,
      GoodsList,
      DetailBottom
    },
    data() {
      return {
        //用来保存传递的商品id
        gid: null,
        //保存轮播图信息
        topImages: [],
        //商品的基本信息
        goodsBase: {},
        //推荐商品数据
        goodsRecommend: {
          page: 0, list: []
        },
        //存放标题栏分类的高度
        themeTops: [0, 400, 550],
        getThemeTopY: null,
        //接受子组件传递的尺寸大小
        getSonSize: '6寸',
        //判断商品是否收藏
        isSelect: false,
        //保存收藏id
        saveCid: 0,
        isGoodsHave: true
      }
    },
    created () {
      //获取通过router传递的id
      this.gid = this.$route.params.gid;
      //根据id获取商品信息
      getDetails(this.gid).then(res => {
        //判断商品是否存在
        if(res != null) {
           //保存轮播图的信息
          this.topImages = res[1];
          //保存商品的基本信息
          this.goodsBase = res[0];
        } else {
          this.isGoodsHave = false
        }
       
      })
      //获取推荐商品的数据
      this.getHomeGoods(this.goodsBase.gkind)

      //获取需要的四个参数
      // this.getThemeTopY = debounce(() => {
      //    this.themeTops = []
      //   this.themeTops.push(0)
      //   this.themeTops.push(this.$refs.kinds.$el.offsetTop)
      //   this.themeTops.push(this.$refs.recommend.$el.offsetTop)
      // })
      //判断是否收藏
      //判断是否登录
      if(this.getUserInfo.uid != 0) {
        //判断是否收藏
        getCollectCid(this.gid,this.getUserInfo.uid).then(res => {
          if(res != 0) {
            //保存cid
            this.saveCid = res;
            this.isSelect = true;
          }
        })
      }
    },
    computed: {
      //获取商品的价格
      getGprice() {
        if(this.getSonSize == '6寸') {
          return this.goodsBase.gsize6
        }else if(this.getSonSize == '8寸') {
          return this.goodsBase.gsize8
        }else if(this.getSonSize == '10寸'){
          return this.goodsBase.gsize10
        }else if(this.getSonSize == '12寸'){
          return this.goodsBase.gsize12
        }else if(this.getSonSize == '14寸'){
          return this.goodsBase.gsize14
        }else if(this.getSonSize == '16寸'){
          return this.goodsBase.gsize16
        }else if(this.getSonSize == '18寸'){
          return this.goodsBase.gsize18
        }else if(this.getSonSize == '20寸'){
          return this.goodsBase.gsize20
        }
      },
      //获取用户信息
      getUserInfo() {
        return this.$store.state.userInfo
      }
    },
    //混入
    mixins: [itemImageMixin, backTopMixin],
    destroyed () {
      //取消全局
      this.$bus.$off('itemImageLoad',this.itemListener)
      
    },
    methods: {
      getHomeGoods(kind) {
        //获取要获取数据的页码
        const page = this.goodsRecommend.page + 1;
        const typepage = kind + "_" + page;
         getHomeGoods(typepage).then(res => {
           //将数据放到数组中
           this.goodsRecommend.list.push(...res);
           this.goodsRecommend.page += 1;
           //刷新
           if(res.length == 0) {
            
           }else{
              this.$refs.scroll.finishPullUp();
           }
        })
      },
      loadMore() {
        this.getHomeGoods(this.goodsBase.gkind);
        this.$refs.scroll.refresh();  
      },
      //监听导航栏传递的参数
      titleClick(index) {
        //根据他们的高度跳转到位置
        this.$refs.scroll.scrollTo(0, -this.themeTops[index], 100)
      },
      //监听位置判断标题栏的状态
      pScroll(position) {
        //置顶按钮的显示与隐藏
        this.isShowBackTop = (-position.y) > 450;
        let scrollTops = -position.y;
        if(scrollTops >=0 && scrollTops <400) {
          this.$refs.nav.currentIndex = 0
        } else if(scrollTops >=400 && scrollTops <550) {
          this.$refs.nav.currentIndex = 1
        } else {
          this.$refs.nav.currentIndex = 2
        }
      },
      //获取子组件中尺寸的大小
      changessactive(currentIndexSize) {
        this.getSonSize = currentIndexSize;
      },
      //加入购物车
      addCarts(uid) {
        //将购物车需要的数据存放到对象中gid gtitle gimage gsize gprice gcount gstatus uid
        const product = {};
        product.gid = this.goodsBase.gid;
        product.sgtitle = this.goodsBase.gtitle;
        product.sgimg = this.goodsBase.gimage;
        product.sgsize = this.getSonSize;
        product.sgprice = this.getGprice;
        product.uid = uid;
        //将商品添加到购物车里
        this.$store.dispatch('addCarts', product).then(res => {
          //toast
          this.$toast.show(res)
        })


        
      },
      //子组件传递修改是否收藏
      selectChange(isSelectZ) {
        //修改父组件的值
        this.isSelect = isSelectZ;
        if(isSelectZ) {          
           //发送请求 增加收藏商品  uid, gid, cgtitle, cgprice, cgimg,cgcollect
           addColect(this.getUserInfo.uid,this.goodsBase.gid,this.goodsBase.gtitle,this.goodsBase.gsize6,this.goodsBase.gimage,
           this.goodsBase.gcollect + 1).then(res => {
             this.saveCid = res;
             this.goodsBase.gcollect += 1;
             this.$toast.show('收藏成功')
             //发送事件
              this.$bus.$emit('changeCounts',this.goodsBase.gid,this.goodsBase.gcollect,this.goodsBase.gkind)
           }) 
        }else {           
           //发送请求 取消收藏
           delColect(this.saveCid,this.goodsBase.gcollect - 1,this.goodsBase.gid).then(res => {
             this.$toast.show('取消收藏')
             this.goodsBase.gcollect -= 1;
             //发送事件
            this.$bus.$emit('changeCounts',this.goodsBase.gid,this.goodsBase.gcollect,this.goodsBase.gkind)
           })
        }
        
        
      },
      //购买商品  添加订单
      insOrder() {
        //添加订单需要的信息  uid  gid  ogtitle ogimg ogsize ogprice ogcount       ogstatus otime
        insOrder(this.getUserInfo.uid,this.goodsBase.gid,this.goodsBase.gtitle,this.goodsBase.gimage,
                  this.getSonSize,this.getGprice,1).then(res => {
           this.$toast.show(res)
           //将页面上的销量+1
           this.goodsBase.gsale += 1;
        })
      }
    }
  }
</script>

<style scoped>
  #detail{
    position: relative;
    z-index: 20;
    background-color: white;
    height: 100vh;
  }
  .det-con{
    position: absolute;
    left: 0px;
    right:0px;
    bottom: 49px;
    top: 44px;
    overflow: hidden;
  }
  .recommends-goods{
    height: 30px;
    line-height: 30px;
    font-size: 18px;
    text-align: center;
    margin: 5px 0;
    color:#ff8198;
  }
  .delgood{
    font-size: 20px;
    text-align: center;
    margin-top: 20px;
  }
</style>