<template>
  <div class="detail-main">
    <div class="kinds">
      <div class="dkinds">
        <div>选择</div>
        <div>{{currentIndexSize}}</div>
        <div class="down-click" @click="changeShow"><img src="~assets/img/down.svg" alt=""></div>
      </div>

      <div class="ckinds" v-show="isShowchanges">
        <div class="ss">
          <div class="sitem" v-for="(item, index) of goodsSize" :key="index">
            <div :class="{sactive : currentIndex == index}" @click="changessactive(index)">{{item}}</div>
          </div>
        </div>
      </div>
    </div>
    <!--蛋糕的参数-->
    <table class="info-param">
      <tr>
        <td class="info-param-key">产地</td>
        <td class="param-value">{{getGplace}}</td>
      </tr>
      <tr>
        <td class="info-param-key">价格</td>
        <td class="param-value">{{getGprice}}</td>
      </tr>
      <tr>
        <td class="info-param-key">保质期</td>
        <td class="param-value">{{getGlife}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  export default {
    name: 'DetailKinds',
    props: {
      //保存商品的种类
      goodsItem: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    data() {
      return {
        //蛋糕尺寸
        goodsSize: ['6寸','8寸','10寸','12寸','14寸','16寸','18寸','20寸'],
        //判断是否显示选择的参数
        isShowchanges: false,
        //尺寸的下标
        currentIndex: 0,
        //下标对应的参数
        currentIndexSize: '6寸'        
      }
    },
    mounted () {    
      // this.changesGoods(this.goodsItem)
    },
    computed: {
      getGlife() {
        return this.goodsItem.glife
      },
      getGprice() {
        return this.goodsItem.gsize6 + "-" + this.goodsItem.gsize20 + "元"
      },
      getGplace() {
        return this.goodsItem.gplace
      }
    },
    methods: {
      //尺寸的点击事件
      changessactive(index) {
        //修改活跃时尺寸的下标
        this.currentIndex = index;
        //将下标所对应的值传递到父组件中
        this.currentIndexSize = this.goodsSize[index];
        this.$emit('changessactive', this.currentIndexSize)
      },
      //点击事件  切换显示参数
      changeShow() {
        if(this.isShowchanges == false){
          this.isShowchanges = true;
        }else{
          this.isShowchanges = false;
        }
      }
    }
  }
</script>

<style scoped>
  .detail-main{
    padding: 0 10px;
  }
  .ckinds{
    padding: 0 20px;
  }
  .dkinds{
    margin-top: 8px;
    display: flex;
    justify-content: space-between;
    height: 30px;
    line-height: 30px;
  }
  .dkinds div{
    flex:1;
    font-size: 14px;
    color: #222;
    text-align: center;
  }
  .ss{
    display: flex;
    height: 80px;
    line-height: 30px;
    margin-top: 5px;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .sitem{
    width: 24%;
    
  }
  .sitem > div{
    margin:auto;
    margin-top: 5px;
    height: 20px;
    width: 50px;
    font-size: 13px;
    line-height: 20px;
    border: 1px solid black;
    text-align: center;

  }
  .sactive{
    background-color: #ff8198
  }
  /*下边框 */
  .kinds{
    border-bottom: 5px solid #f2f5f8;
    padding-bottom: 5px;

  }

  .info-param {
    margin-top: 10px;
    width: 100%;
    border-bottom: 5px solid #f2f5f8;
    padding-bottom: 5px;
  }
  .info-param tr{
    border-bottom: 1px solid rgba(0,0,0,1);
    height: 28px;
    line-height: 28px;
  }
  .info-param-key {
    /*当value的数据量比较大的时候, 会挤到key,所以给一个固定的宽度*/
    width: 95px;
  }
  .param-value {
    color: #eb4868
  }
  .down-click img{
    height:30px;
    width: 30px;
  }
</style>