<template>
    <swiper class="dswiper">
      <swiper-item v-for="(item, index) in topImages" :key="index">
        <img :src="item.iimagesrc" alt="">
      </swiper-item>
    </swiper>
</template>

<script>
  import {Swiper, SwiperItem} from 'common/swiper/index'
  export default {
    name: 'DetailSwiper',
    components: {
      Swiper,
      SwiperItem
    },
    props: {
      //保存轮播图信息
      topImages: {
        type: Array,
        default() {
          return []
        }
      }
    }
  }
</script>

<style scoped>
  .dswiper{
    height: 300px;  
    overflow: hidden;
  }
  .dswiper img{
    min-height: 300px;
  }
</style>